.event {
  display: -ms-flexbox;
  display: flex;
}

.event-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  cursor: pointer;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 7px 0;
  padding-left: 5px;
  margin-left: 20px;
  line-height: 1.25em;
}

.complete .event-info {
  color: var(--grayed-out);
}

.event-info:hover {
  background-color: #eaeaea
}

.title {
  font-weight: 600;
  width: 100%;
}

.time {
  text-align: center;
  padding-right: 30px;
}

.time:empty {
  display: none;
}

.time, .details {
  font-size: .9em;
}

@media (min-width: 860px) {
  .event-info {
    margin: 0;
  }

  .time {
    -ms-flex-order: -1;
        order: -1;
    padding: 0 10px;
    width: 12%;
  }

  .time:empty {
    display: block;
  }

  .title {
    width: 88%;
    padding: 0 10px;
  }

  .details {
    margin-left: 12%;
    padding: 0 10px;
  }
}


/* Toggle */

.event-toggle {
  padding: 7px 10px;
  padding-left: 0;
}

.event-toggle button {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: inherit;
  border: 2px solid var(--font-color);
  border-radius: 5px;
  width: 21px;
  height: 21px;
  cursor: pointer;
}

.event-toggle button:hover {
  opacity: 0.5;
}

.complete button {
  background-color: var(--grayed-out);
  border-color: var(--grayed-out);
}

.event-toggle button:focus {
  outline: none
}


