#header {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 5px;
  height: 70px;
  background-color: white;
  border-bottom: 1px solid var(--border-color);
  --separation: 5px;
}

#calendar {
  margin-right: var(--separation);
}

h1 {
  font-size: 2em;
  display: none;
  cursor: default;
  font-family: 'Poiret One', sans-serif;
}

@media (min-width: 675px) {
  h1 {
    margin-right: var(--separation);
    display: block;
  }
}

.nav {
  margin-right: var(--separation);
}

/* header button */

#header button {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: inherit;
  border: none;
  cursor: default;
  font-size: .95em;
  padding: 5px 10px;
  color: var(--font-color);
  background-color: #f5f5f5;
  cursor: pointer;
  border-radius: 7px;
  font-weight: 300;
}

#header button:hover,
#header button:focus {
  background-color: #dedede;
}

#header button:focus {
  outline: none;
}

/* selection triangle */

.select {
  display: none;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-top: 6px solid var(--font-color);
  margin-left: 15px;
}

@media (min-width: 490px) {
  #header {
    --separation: 35px;
    padding: 0 15px;
  }

  #header button {
    font-size: 1.1em;
    padding: 5px 15px;
  }

  .select {
    display: inline-block;
  }
}


